import styled, { keyframes } from "styled-components";

import { colors, marginsCss } from "../styleguide";

import { SkeletonLoaderProps } from "./SkeletonLoader.types";

const opacityPulse = keyframes`
  0% {opacity: 0.5}
  50% {opacity: 1}
  100%{opacity: 0.5}
`;


export const StyledSkeletonLoader = styled.div<Omit<SkeletonLoaderProps, "dataQA">>`
	height: ${({ height }) => height || "20px"};
	width: ${({ width }) => width};
	border-radius: 4px;
	background-color: ${colors.lightGray};
	animation: ${opacityPulse} 1.5s infinite;
	${marginsCss};
`;
